<template>
  <v-container fluid>
    <v-row align="center" justify="center">
      <span> {{ cylinders }} </span>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "CylinderView",

  props: {
    cruiseData: {
      type: Array,
      default: () => [],
    },
    interpLoading: {
      type: Boolean,
      default: () => false,
    },
    newSelectedCylinders: {
      type: Array,
      default: () => [],
    },
  },

  data: () => ({
    cylinders: [],
  }),

  mounted: function () {
    let vm = this;

    vm.cylinders = vm.newSelectedCylinders; // get incoming cylinders
  },

  watch: {
    newSelectedCylinders: function (newCylindersSelected) {
      let vm = this;
      const curCylSorted = vm.cylinders.slice().sort();
      const newCylSorted = newCylindersSelected.slice().sort();
      const same =
        curCylSorted.length === newCylSorted.length &&
        curCylSorted.every(function (value, index) {
          return value === newCylSorted[index];
        });
      if (!same) {
        // only update cylinder list if incoming list is different from current selection
        vm.cylinders = newCylindersSelected;
      }
    },
  },
};
</script>

