<template>
  <v-container fluid>
    <!-- TOP -->
    <v-row id="map-top-controls-container">
      <!-- TOP-LEFT -->
      <v-col cols="1">
        <v-icon @click="resetView">mdi-restart</v-icon>
      </v-col>

      <!-- TOP-RIGHT -->
      <v-col cols="11">
        <v-row>
          <v-col cols="4">
            <v-select
              outlined
              dense
              hide-details
              label="Background"
              v-model="background"
              :menu-props="{ bottom: true, offsetY: true }"
              :items="backgrounds[cruiseFile]"
            ></v-select>
          </v-col>

          <v-col cols="4">
            <v-select
              outlined
              dense
              hide-details
              label="Site"
              v-model="site"
              :menu-props="{ bottom: true, offsetY: true }"
              :items="sites"
            ></v-select>
          </v-col>

          <!-- BUTTON ARRAY -->
          <v-col cols="4" class="pl-1 text-right">
            <!-- SELECTED BORDER TOGGLE -->
            <v-btn
              icon
              small
              :disabled="!cylinders.selection.border.exists"
              :color="
                cylinders.selection.border.exists &&
                cylinders.selection.border.show
                  ? 'primary'
                  : 'default'
              "
              @click="toggleSelectionBorder"
            >
              <v-icon
                v-if="
                  cylinders.selection.border.exists &&
                  cylinders.selection.border.show
                "
              >
                mdi-border-outside
              </v-icon>
              <v-icon v-else>mdi-border-none</v-icon>
            </v-btn>

            <!-- MANUAL DRAG MODE TOGGLE -->
            <v-btn
              v-if="interpMode == 'Manual'"
              icon
              small
              color="primary"
              @click="dragMode = dragMode == 'Zoom' ? 'Select' : 'Zoom'"
            >
              <v-icon v-if="dragMode == 'Select'">
                mdi-selection-ellipse-arrow-inside
              </v-icon>
              <v-icon v-if="dragMode == 'Zoom'">
                mdi-magnify-plus-outline
              </v-icon>
            </v-btn>

            <!-- FILE DRAG MODE NO TOGGLE -->
            <v-btn
              v-if="interpMode == 'File'"
              disabled
              icon
              small
              color="primary"
            >
              <v-icon>mdi-magnify-plus-outline</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <!-- MIDDLE -->
    <v-row id="plot-container" ref="plotContainer" justify="center">
      <svg id="svg"></svg>
    </v-row>
  </v-container>
</template>

<script>
import $ from "jquery";
import * as d3 from "d3";

/**
 * Haversine formula. Generally used geo measurement function.
 * See: https://www.movable-type.co.uk/scripts/latlong.html
 */
function hav(lat1, lon1, lat2, lon2) {
  const R = 6371e3; // metres
  const phi1 = (lat1 * Math.PI) / 180; // phi, lambda in radians
  const phi2 = (lat2 * Math.PI) / 180;
  const dPhi = ((lat2 - lat1) * Math.PI) / 180;
  const dLambda = ((lon2 - lon1) * Math.PI) / 180;
  const a =
    Math.sin(dPhi / 2) * Math.sin(dPhi / 2) +
    Math.cos(phi1) *
      Math.cos(phi2) *
      Math.sin(dLambda / 2) *
      Math.sin(dLambda / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const d = R * c; // in metres
  return d * 100; // in centimetres
}

export default {
  name: "MapView",

  props: {
    cruiseFile: {
      type: String,
      default: () => "",
    },
    cruiseData: {
      type: Array,
      default: () => [],
    },
    interpMode: {
      type: String,
      default: () => "",
    },
    newSelectedCylinders: {
      type: Array,
      default: () => [],
    },
  },

  data: () => ({
    publicPath: process.env.BASE_URL,

    // plot variables
    svgWidth: undefined,
    svgHeight: undefined,
    plotMargins: undefined,
    plotWidth: undefined,
    plotHeight: undefined,
    svg: undefined,
    plotGroup: undefined,
    bgGroup: undefined,
    marksGroup: undefined,
    xScale: undefined,
    xAxis: undefined,
    xAxisGroup: undefined,
    xGridlinesGroup: undefined,
    yScale: undefined,
    yAxis: undefined,
    yAxisGroup: undefined,
    yGridlinesGroup: undefined,

    // settings
    dragMode: "Zoom",
    background: "None",
    backgrounds: {
      "Pescadero_final.csv": [
        "None",
        "Auka_LASSmb_Topo5cm_slope3710-3630",
        // "Auka_Lidar_Topo2cm_slope3710-3630",
        "Auka_Pmos_LeftRaw_5cm",
        "MAUV_SoPB_TopoSq_1m_slope3796-3334",
      ],
      "SantaMonica_GeoChem_repeat.csv": [
        "None",
        "SantaMonica_650mMound_MAUV_Topo1m_slope820-560",
        // "SantaMonica_800mMound_LASS_PmosRightRaw1cm",
        "SantaMonica_800mMound_LASS_Topo5cm_slope825-800",
        "SantaMonica_800mMound_MAUV_Topo1m_slope880-760",
        "SantaMonica_MAUV_Topo2m_slope900-50",
        "SantaMonica_NWMounds_MAUV_Topo1m_slope625-500",
      ],
    },
    background_data: undefined,
    filter: undefined,

    // data
    cylinders: {
      all: [],
      selected: [],
      selection: {
        allowed: true,
        border: {
          show: true,
          exists: false,
        },
      },
    },
    xVar: "Longitude_deg",
    yVar: "Latitude_deg",
    site: "All",
    sites: [],
  }),

  mounted: function () {
    let vm = this;
    vm.svg = d3.select("#svg"); // get svg once mounted

    // refresh the svg on resize
    $(window).on("resize", () => {
      vm.init();
      if (vm.background && vm.background !== "None") {
        vm.loadBackgroundData().then(vm.draw);
      } else {
        vm.draw();
      }
    });

    // if File mode no selections!
    if (vm.interpMode == "File") vm.cylinders.selection.allowed = false;

    // start the show!
    vm.init();
    vm.parseData(vm.cruiseData);
    vm.filterBySite();
    if (vm.background && vm.background !== "None") {
      vm.loadBackgroundData().then(vm.draw);
    } else {
      vm.draw();
    }
  },

  watch: {
    cruiseData: function (newData) {
      let vm = this;
      vm.site = "All"; // reset site
      vm.background = "None"; // make sure no background loads
      vm.init();
      vm.parseData(newData);
      vm.draw();
    },

    interpMode: function (newInterpMode) {
      let vm = this;
      if (newInterpMode == "File") {
        vm.dragMode = "Zoom"; // reset select mode
        vm.cylinders.selection.allowed = false; // don't allow selection
        vm.draw(); // re-draw to remove any existing selections
      } else if (newInterpMode == "Manual") {
        vm.cylinders.selection.allowed = true; // allow selections!
      }
    },

    newSelectedCylinders: function (newCylindersSelected) {
      let vm = this;
      const curCylSorted = vm.cylinders.selected.slice().sort();
      const newCylSorted = newCylindersSelected.slice().sort();
      const same =
        curCylSorted.length === newCylSorted.length &&
        curCylSorted.every(function (value, index) {
          return value === newCylSorted[index];
        });
      if (!same) {
        // only update selected list if incoming list is different from current selection
        vm.cylinders.selected = newCylindersSelected;
        vm.draw();
      }
    },

    background: function () {
      let vm = this;
      vm.init();
      if (vm.background && vm.background !== "None") {
        vm.loadBackgroundData().then(vm.draw);
      } else {
        vm.draw();
      }
    },

    site: function (newSite) {
      let vm = this;
      vm.$emit("updateSite", newSite);
      vm.filterBySite();
      vm.draw();
    },
  },

  methods: {
    init() {
      let vm = this;

      // define background image files
      vm.background_data = {
        meta: undefined,
        tif: `${vm.publicPath}assets/backgrounds/${vm.background}.tif`,
        png: `${vm.publicPath}assets/backgrounds/${vm.background}.png`,
        json: `${vm.publicPath}assets/backgrounds/${vm.background}.json`,
      };

      // get layout
      const containerWidth = this.$refs.plotContainer.clientWidth;
      const remainingHeight = window.innerHeight - 64 - 64 - 24; // app-bar, map-top-controls-container, container-padding
      vm.svgWidth = Math.min(containerWidth, remainingHeight);
      vm.svgHeight = Math.min(containerWidth, remainingHeight);

      // set plot margins, width and height
      vm.plotMargins = { top: 10, bottom: 60, left: 60, right: 10 };
      vm.plotWidth = vm.svgWidth - vm.plotMargins.left - vm.plotMargins.right;
      vm.plotHeight = vm.svgHeight - vm.plotMargins.top - vm.plotMargins.bottom;

      // update svg
      vm.svg.selectAll("*").remove(); // clear out any objects already in the svg
      vm.svg.attr("width", vm.svgWidth).attr("height", vm.svgHeight);

      // Add plot group, containing axes, marks, and legend
      vm.plotGroup = vm.svg
        .append("g")
        .classed("plot", true)
        .attr(
          "transform",
          `translate(${vm.plotMargins.left},${vm.plotMargins.top})`
        );

      // add background group
      vm.bgGroup = vm.plotGroup.append("g").classed("bg", true);

      // add svg to the background group to occlude oversized children
      vm.bgGroup
        .append("svg")
        .attr("width", vm.plotWidth)
        .attr("height", vm.plotHeight);

      // if a background is selected, add it to the background svg
      if (vm.background && vm.background !== "None") {
        vm.bgGroup
          .select("svg")
          .append("svg:image")
          .attr("preserveAspectRatio", "none")
          .attr("xlink:href", vm.background_data.png);
      }

      // Add X and Y axis groups
      vm.yAxisGroup = vm.plotGroup
        .append("g")
        .classed("y", true)
        .classed("axis", true);
      vm.xAxisGroup = vm.plotGroup
        .append("g")
        .classed("x", true)
        .classed("axis", true)
        .attr("transform", `translate(${0},${vm.plotHeight})`);

      // add gridlines groups
      vm.xGridlinesGroup = vm.plotGroup
        .append("g")
        .classed("x-gridlines", true);
      vm.yGridlinesGroup = vm.plotGroup
        .append("g")
        .classed("y-gridlines", true);

      // Add data marks group
      vm.marksGroup = vm.plotGroup.append("g").classed("marks", true);

      // add svg to the marks group to occlude oversized children
      vm.marksGroup
        .append("svg")
        .attr("width", vm.plotWidth)
        .attr("height", vm.plotHeight);

      // Add drag region to marks group
      let dragGroup = vm.marksGroup.append("g").classed("drag", true);
      let dragRegion = dragGroup
        .append("rect")
        .attr("width", vm.plotWidth)
        .attr("height", vm.plotHeight)
        .style("opacity", 0)
        .on("contextmenu", (e) => {
          e.preventDefault();
          if (vm.dragMode == "Zoom") {
            // right-click to restore the previous filter, if possible
            if (vm.filter.stack.length > 0) {
              vm.filter = vm.filter.stack.pop();
              vm.draw();
            }
          }
          return false;
        });

      // define the selection rectangle
      let selectionRect = {
        element: null,
        previousElement: null,
        container: vm.marksGroup,
        currentY: 0,
        currentX: 0,
        originX: 0,
        originY: 0,
        init: function (newX, newY) {
          let rectElement = this.container
            .append("rect")
            .attr("x", 0)
            .attr("y", 0)
            .attr("width", 0)
            .attr("height", 0)
            .attr("rx", 4)
            .attr("ry", 4)
            .attr("stroke", "#545454")
            .attr("stroke-width", "2px")
            .attr("stroke-opacity", "1")
            .attr("fill", "white")
            .attr("fill-opacity", "0.5")
            .classed("selection", true);
          this.setElement(rectElement);
          this.originX = newX;
          this.originY = newY;
          this.update(newX, newY);
        },
        update: function (newX, newY) {
          this.currentX = newX;
          this.currentY = newY;
          const x = this.currentX < this.originX ? this.currentX : this.originX;
          const y = this.currentY < this.originY ? this.currentY : this.originY;
          const rectWidth = Math.abs(this.currentX - this.originX);
          const rectHeight = Math.abs(this.currentY - this.originY);
          this.element
            .attr("x", x)
            .attr("y", y)
            .attr("width", rectWidth)
            .attr("height", rectHeight);
        },
        setElement: function (ele) {
          this.previousElement = this.element;
          this.element = ele;
        },
        remove: function () {
          this.element.remove();
          this.element = null;
        },
        removePrevious: function () {
          if (this.previousElement) {
            this.previousElement.remove();
          }
        },
        getCurrentAttributes: function () {
          const x = +this.element.attr("x");
          const y = +this.element.attr("y");
          const rectWidth = +this.element.attr("width");
          const rectHeight = +this.element.attr("height");
          return {
            x1: x,
            y1: y,
            x2: x + rectWidth,
            y2: y + rectHeight,
          };
        },
      };

      let dragStart = (e) => {
        selectionRect.init(e.x, e.y);
        selectionRect.removePrevious();
      };

      let dragMove = (e) => {
        let newX = e.x < 0 ? 0 : e.x > vm.plotWidth ? vm.plotWidth : e.x;
        let newY = e.y < 0 ? 0 : e.y > vm.plotHeight ? vm.plotHeight : e.y;
        selectionRect.update(newX, newY);
      };

      let dragEnd = () => {
        let attrs = selectionRect.getCurrentAttributes();
        selectionRect.remove();
        if (attrs.x2 - attrs.x1 > 1 && attrs.y2 - attrs.y1 > 1) {
          // large enough drag region was made, attempt action
          const xMin = vm.xScale.invert(attrs.x1);
          const xMax = vm.xScale.invert(attrs.x2);
          const yMin = vm.yScale.invert(attrs.y2);
          const yMax = vm.yScale.invert(attrs.y1);
          switch (vm.dragMode) {
            case "Zoom":
              vm.filter.stack.push($.extend(true, {}, vm.filter)); // push the prior filter values onto the filter stack
              vm.setXYFilter(xMin, xMax, yMin, yMax); // update filter values
              vm.squareFilters(); // apply transformations to the filters
              break;
            case "Select":
              // filter list of data points
              vm.cylinders.selected = vm.cylinders.all
                .filter((p) => {
                  return (
                    p.xVar >= xMin &&
                    p.xVar <= xMax &&
                    p.yVar >= yMin &&
                    p.yVar <= yMax
                  );
                })
                .map((p) => p.Cylinder);
              vm.$emit("updateSelectedCylinders", vm.cylinders.selected);
              break;
          }
        } else {
          // drag region too small, interpret as single click
          if (vm.dragMode == "Select") {
            vm.cylinders.selected = []; // remove selected cylinders
            vm.$emit("updateSelectedCylinders", vm.cylinders.selected);
          }
        }
        vm.draw(); // re-draw the vis
      };

      let dragBehavior = d3
        .drag()
        .on("start", dragStart)
        .on("drag", dragMove)
        .on("end", dragEnd);

      dragRegion.call(dragBehavior);
    },

    parseData(data) {
      let vm = this;

      // create list of data points with only id columns
      const rawCylinderList = data.map((p) => {
        return {
          Cylinder: p["Cylinder"],
          Site: p["Site"],
          xVar: parseFloat(p[vm.xVar]),
          yVar: parseFloat(p[vm.yVar]),
        };
      });

      // remove duplicate data points by Cylinder name (keep the last one found)
      vm.cylinders.all = [
        ...new Map(
          rawCylinderList.map((item) => [item["Cylinder"], item])
        ).values(),
      ];

      // set filter object to initial values
      vm.filter = {
        x: [0, 100],
        y: [0, 100],
        dx: 100,
        dy: 100,
        stack: [],
        extent: {
          x: [0, 100],
          y: [0, 100],
        },
        hav_dist: {
          x: {
            y_min: 100,
            y_max: 100,
          },
          y: 100,
        },
      };

      let siteList = ["All"];

      // set initial filter to first point
      let p = vm.cylinders.all[0];
      let xMin = p.xVar;
      let xMax = p.xVar;
      let yMin = p.yVar;
      let yMax = p.yVar;
      siteList.push(p["Site"]);

      // collect points
      for (let i = 1; i < vm.cylinders.all.length; i++) {
        p = vm.cylinders.all[i];
        xMin = Math.min(xMin, p.xVar);
        xMax = Math.max(xMax, p.xVar);
        yMin = Math.min(yMin, p.yVar);
        yMax = Math.max(yMax, p.yVar);
        siteList.push(p["Site"]);
      }

      // get unique sites
      vm.sites = [...new Set(siteList)];

      // apply transformations to the filters
      vm.setXYFilter(xMin, xMax, yMin, yMax);
      vm.squareFilters();
      vm.padFilters();

      // finally set the extents to the current filters
      vm.setXYExtent(
        vm.filter.x[0],
        vm.filter.x[1],
        vm.filter.y[0],
        vm.filter.y[1]
      );
    },

    setXYFilter(xMin, xMax, yMin, yMax) {
      let vm = this;
      // store values
      vm.filter.x[0] = xMin;
      vm.filter.x[1] = xMax;
      vm.filter.y[0] = yMin;
      vm.filter.y[1] = yMax;
      // get raw diffs
      vm.filter.dx = xMax - xMin;
      vm.filter.dy = yMax - yMin;
      // get haversine dist (in cm)
      vm.filter.hav_dist.x.y_min = hav(yMin, xMin, yMin, xMax);
      vm.filter.hav_dist.x.y_max = hav(yMax, xMin, yMax, xMax);
      vm.filter.hav_dist.y = hav(yMin, xMin, yMax, xMin);
    },

    setXYExtent(xMin, xMax, yMin, yMax) {
      let vm = this;
      vm.filter.extent.x[0] = xMin;
      vm.filter.extent.x[1] = xMax;
      vm.filter.extent.y[0] = yMin;
      vm.filter.extent.y[1] = yMax;
    },

    minFilters() {
      let vm = this;
      // if diff is < ~50m, apply a buffer to get extents to be ~50m
      let bufferX = vm.filter.dx < 5e-4 ? (5e-4 - vm.filter.dx) / 2 : 0;
      let bufferY = vm.filter.dy < 5e-4 ? (5e-4 - vm.filter.dy) / 2 : 0;
      if ((bufferX > 0) | (bufferY > 0)) {
        vm.setXYFilter(
          vm.filter.x[0] - bufferX,
          vm.filter.x[1] + bufferX,
          vm.filter.y[0] - bufferY,
          vm.filter.y[1] + bufferY
        );
      }
    },

    squareFilters() {
      let vm = this;
      // make the extents square
      let adjustX =
        vm.filter.dx < vm.filter.dy ? (vm.filter.dy - vm.filter.dx) / 2 : 0;
      let adjustY =
        vm.filter.dy < vm.filter.dx ? (vm.filter.dx - vm.filter.dy) / 2 : 0;
      if ((adjustX > 0) | (adjustY > 0)) {
        vm.setXYFilter(
          vm.filter.x[0] - adjustX,
          vm.filter.x[1] + adjustX,
          vm.filter.y[0] - adjustY,
          vm.filter.y[1] + adjustY
        );
      }
    },

    padFilters() {
      let vm = this;
      // pad the filters by 1/20 of viewport window to give a border around points
      const padX = vm.filter.dx / 20;
      const padY = vm.filter.dy / 20;
      vm.setXYFilter(
        vm.filter.x[0] - padX,
        vm.filter.x[1] + padX,
        vm.filter.y[0] - padY,
        vm.filter.y[1] + padY
      );
    },

    filterBySite() {
      let vm = this;
      let xMin, xMax, yMin, yMax;
      if (vm.site == "All") {
        // set initial filter to first point
        let p = vm.cylinders.all[0];
        xMin = p.xVar;
        xMax = p.xVar;
        yMin = p.yVar;
        yMax = p.yVar;
        for (let i = 1; i < vm.cylinders.all.length; i++) {
          p = vm.cylinders.all[i];
          xMin = Math.min(xMin, p.xVar);
          xMax = Math.max(xMax, p.xVar);
          yMin = Math.min(yMin, p.yVar);
          yMax = Math.max(yMax, p.yVar);
        }
      } else {
        // set initial filter to first point contained in the filter
        let i = 0;
        while (vm.site !== vm.cylinders.all[i]["Site"]) i++;
        let p = vm.cylinders.all[i];
        xMin = p.xVar;
        xMax = p.xVar;
        yMin = p.yVar;
        yMax = p.yVar;
        i++;
        for (i; i < vm.cylinders.all.length; i++) {
          p = vm.cylinders.all[i];
          if (vm.site == p["Site"]) {
            xMin = Math.min(xMin, p.xVar);
            xMax = Math.max(xMax, p.xVar);
            yMin = Math.min(yMin, p.yVar);
            yMax = Math.max(yMax, p.yVar);
          }
        }
      }
      vm.filter.stack = []; // start a new filter stack
      vm.setXYFilter(xMin, xMax, yMin, yMax);
      vm.minFilters();
      vm.squareFilters();
      vm.padFilters();
    },

    resetFilters() {
      let vm = this;
      vm.site = "All";
      vm.$emit("updateSite", "All"); // update prop
      vm.filter.stack = []; // reset filter stack
      vm.setXYFilter(
        vm.filter.extent.x[0],
        vm.filter.extent.x[1],
        vm.filter.extent.y[0],
        vm.filter.extent.y[1]
      );
    },

    resetView() {
      let vm = this;
      vm.resetFilters();
      vm.draw();
    },

    toggleSelectionBorder() {
      let vm = this;
      vm.cylinders.selection.border.show = !vm.cylinders.selection.border.show;
      d3.select(".bound-box").attr("opacity", () =>
        vm.cylinders.selection.border.show ? 1 : 0
      );
    },

    async loadBackgroundData() {
      let vm = this;
      // const tiff = await fetch(vm.background_data.tif).then((response) =>
      //   response.arrayBuffer().then((buffer) => GeoTIFF.fromArrayBuffer(buffer))
      // );
      // const image = await tiff.getImage();
      // vm.background_data.meta = {
      //   width: image.getWidth(),
      //   height: image.getHeight(),
      //   tileWidth: image.getTileWidth(),
      //   tileHeight: image.getTileHeight(),
      //   samplesPerPixel: image.getSamplesPerPixel(),
      //   origin: image.getOrigin(),
      //   resolution: image.getResolution(),
      //   bbox: image.getBoundingBox(),
      // };
      vm.background_data.meta = await fetch(vm.background_data.json).then(
        (response) => response.text().then((contents) => JSON.parse(contents))
      );
      return vm.background_data.meta;
    },

    draw() {
      let vm = this;
      if (!vm.cylinders.all) return; // if there's no dataset don't update the scatter plot

      // filter list of data points
      let prepared = vm.cylinders.all.filter((p) => {
        return (
          p.xVar >= vm.filter.x[0] &&
          p.xVar <= vm.filter.x[1] &&
          p.yVar >= vm.filter.y[0] &&
          p.yVar <= vm.filter.y[1]
        );
      });

      // get haversine x distance formatted with unit
      let xOutput;
      const xDist = vm.filter.hav_dist.x.y_min; // using haversine x at y_min in cm
      if (xDist < 100) {
        xOutput = `${Math.round((xDist + Number.EPSILON) * 100) / 100}cm`;
      } else if (xDist < 100000) {
        xOutput = `${Math.round((xDist / 100 + Number.EPSILON) * 100) / 100}m`;
      } else if (xDist < 100000000) {
        xOutput = `${
          Math.round((xDist / 100000 + Number.EPSILON) * 100) / 100
        }km`;
      } else {
        xOutput = "Unknown";
      }

      // get haversine y distance formatted with unit
      let yOutput;
      const yDist = vm.filter.hav_dist.y; // using haversine y at x_min in cm
      if (yDist < 100) {
        yOutput = `${Math.round((yDist + Number.EPSILON) * 100) / 100}cm`;
      } else if (yDist < 100000) {
        yOutput = `${Math.round((yDist / 100 + Number.EPSILON) * 100) / 100}m`;
      } else if (yDist < 100000000) {
        yOutput = `${
          Math.round((yDist / 100000 + Number.EPSILON) * 100) / 100
        }km`;
      } else {
        yOutput = "Unknown";
      }

      // Create scales and axes based on vis matrix
      let xAxisTitle = `Longitude | decimal degrees | ${xOutput}`;
      let yAxisTitle = `Latitude | decimal degrees | ${yOutput}`;

      // set scales
      vm.xScale = d3.scaleLinear().domain(vm.filter.x).range([0, vm.plotWidth]);
      vm.yScale = d3
        .scaleLinear()
        .domain(vm.filter.y)
        .range([vm.plotHeight, 0]);

      // set axes
      vm.xAxis = d3.axisBottom(vm.xScale);
      vm.yAxis = d3.axisLeft(vm.yScale);

      // draw axes
      vm.xAxisGroup.call(vm.xAxis);
      vm.yAxisGroup.call(vm.yAxis);

      // draw axis titles
      vm.xAxisGroup.select(".x.axis.title").remove();
      vm.xAxisGroup
        .append("g")
        .classed("x axis title", true)
        .attr("opacity", 1)
        .attr("transform", `translate(${vm.plotWidth / 2}, 0)`)
        .append("text")
        .attr("text-anchor", "middle")
        .attr("fill", "currentColor")
        .attr("dy", "2.9em")
        .text(xAxisTitle);
      vm.yAxisGroup.select(".y.axis.title").remove();
      vm.yAxisGroup
        .append("g")
        .classed("y axis title", true)
        .attr("opacity", 1)
        .attr("transform", `translate(-25, ${vm.plotHeight / 2})`)
        .append("text")
        .attr("fill", "currentColor")
        .text(yAxisTitle);

      // prepare data labels for yAxis
      vm.yAxisGroup
        .selectAll("text")
        .style("text-anchor", "middle")
        .attr("dx", "0.8em")
        .attr("dy", "-1.21em")
        .attr("transform", "rotate(-90)");

      // stagger every other tick label
      vm.xAxisGroup
        .selectAll(".tick line")
        .attr("y2", (_, i) => (i % 2 == 0 ? 16 : 4));
      vm.xAxisGroup
        .selectAll(".tick text")
        .attr("dy", (_, i) => (i % 2 == 0 ? "2em" : "0.75em"));
      vm.yAxisGroup
        .selectAll(".tick line")
        .attr("x2", (_, i) => (i % 2 == 0 ? -16 : -4));
      vm.yAxisGroup
        .selectAll(".tick text")
        .attr("dy", (_, i) => (i % 2 == 0 ? "-2em" : "-0.75em"));

      // add gridlines
      let xGridlines = d3
        .axisTop()
        .tickFormat("")
        .tickSize(-vm.plotHeight)
        .scale(vm.xScale);
      vm.xGridlinesGroup.select("*").remove();
      vm.xGridlinesGroup
        .call(xGridlines)
        .call((g) => g.select(".domain").remove());
      let yGridlines = d3
        .axisRight()
        .tickFormat("")
        .tickSize(vm.plotWidth)
        .scale(vm.yScale);
      vm.yGridlinesGroup.select("*").remove();
      vm.yGridlinesGroup
        .call(yGridlines)
        .call((g) => g.select(".domain").remove());

      // Define the div for the tooltip
      d3.selectAll(".tooltip").remove();
      let div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip")
        .style("opacity", 0);

      // JOIN data selection
      let dataBound = vm.marksGroup.selectAll(".post").data(prepared);

      // DELETE extra points
      dataBound.exit().remove();

      // ENTER new points
      let enterSelection = dataBound.enter().append("g").classed("post", true);

      // UPDATE all existing points
      enterSelection.append("circle");
      enterSelection
        .merge(dataBound)
        .select("circle")
        .attr(
          "transform",
          (d) => `translate(${vm.xScale(d["xVar"])},${vm.yScale(d["yVar"])})`
        )
        .attr("r", 6)
        .style("fill", "white")
        .style("fill-opacity", 0.8)
        .style("stroke-width", (d) => {
          const selected = vm.cylinders.selected.indexOf(d.Cylinder) !== -1;
          return selected ? "3px" : "1px";
        })
        .style("stroke", (d) => {
          const selected = vm.cylinders.selected.indexOf(d.Cylinder) !== -1;
          return selected ? "red" : "black";
        })
        .style("cursor", "pointer")
        .on("click", function (_, d) {
          if (vm.cylinders.selection.allowed) {
            // toggle selected state of the clicked point
            const index = vm.cylinders.selected.indexOf(d.Cylinder);
            if (index !== -1) {
              d3.select(this)
                .style("stroke", "black")
                .style("stroke-width", "1px");
              vm.cylinders.selected.splice(index, 1);
              vm.$emit("updateSelectedCylinders", vm.cylinders.selected);
            } else {
              d3.select(this)
                .style("stroke", "red")
                .style("stroke-width", "3px");
              vm.cylinders.selected.push(d.Cylinder);
              vm.$emit("updateSelectedCylinders", vm.cylinders.selected);
            }
            // update bounding box, if possible
            vm.drawBoundingBox();
          }
        })
        .on("mouseover", function (e, d) {
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html(`${d.Cylinder}<br />${d.Site}<br />(${d.yVar}, ${d.xVar})`)
            .style("left", e.x + 10 + "px")
            .style("top", e.y - 45 + "px");
        })
        .on("mousemove", function (e) {
          div.style("left", e.x + 10 + "px").style("top", e.y - 45 + "px");
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        });

      // draw bounding box around selected points, if possible
      vm.drawBoundingBox();

      // finally update the background PNG
      if (vm.background && vm.background !== "None") {
        vm.bgGroup
          .select("image")
          .attr("x", vm.xScale(vm.background_data.meta.origin[0]))
          .attr("y", vm.yScale(vm.background_data.meta.origin[1]))
          .attr(
            "width",
            vm.xScale(vm.background_data.meta.bbox[2]) -
              vm.xScale(vm.background_data.meta.bbox[0])
          )
          .attr(
            "height",
            vm.yScale(vm.background_data.meta.bbox[1]) -
              vm.yScale(vm.background_data.meta.bbox[3])
          );
      }
    },

    drawBoundingBox() {
      let vm = this;

      // remove bounding box around selected points, if possible
      vm.cylinders.selection.border.exists = false;
      vm.marksGroup.select(".bound-box").remove();

      if (vm.cylinders.selected.length > 1) {
        // get all selected cylinders from all cylinders list
        const selectedSet = new Set(vm.cylinders.selected);
        const points = [...new Set(vm.cylinders.all)].filter((x) =>
          selectedSet.has(x.Cylinder)
        );

        // get bounding box coords around points
        let xMin, xMax, yMin, yMax;
        let p = points[0];
        xMin = p.xVar;
        xMax = p.xVar;
        yMin = p.yVar;
        yMax = p.yVar;
        for (let i = 1; i < points.length; i++) {
          p = points[i];
          xMin = Math.min(xMin, p.xVar);
          xMax = Math.max(xMax, p.xVar);
          yMin = Math.min(yMin, p.yVar);
          yMax = Math.max(yMax, p.yVar);
        }

        // convert coords to px, where (x1, y1) is top-left and (x2, y2) is bottom-right
        let x1 = vm.xScale(xMin);
        let x2 = vm.xScale(xMax);
        let y1 = vm.yScale(yMax);
        let y2 = vm.yScale(yMin);

        // at least one side of the boundary has to be at least 1x point diameter
        // in order to resolve the boundary. that is, the centers of the points
        // inside the boundary have to be at least 1x diameter apart, which
        // would imply they are just touching on either axis

        if (x2 - x1 > 12 || y2 - y1 > 12) {
          // pad the bounds by 1x diameter to make it surround the points nicely
          const pad = 12;
          x1 -= pad;
          x2 += pad;
          y1 -= pad;
          y2 += pad;

          // create the bounding box group
          let bboxGroup = vm.marksGroup
            .select("svg")
            .append("g")
            .classed("bound-box", true)
            .attr("opacity", () =>
              vm.cylinders.selection.border.show ? 1 : 0
            );

          // add border rectangle
          bboxGroup
            .append("rect")
            .attr("x", x1)
            .attr("y", y1)
            .attr("width", x2 - x1)
            .attr("height", y2 - y1)
            .attr("rx", 4)
            .attr("ry", 4)
            .attr("stroke", "#ffffff")
            .attr("stroke-width", "3px")
            .attr("stroke-opacity", 1)
            .attr("fill", "white")
            .attr("fill-opacity", 0.1);

          // add unicorn horn
          const a = Math.max(0.15 * (x2 - x1), 16); // side length => MAX 10% of width or 16px
          const h = (Math.sqrt(3) / 2) * a; // height of equilateral triangle with sides a
          const cx = (x2 + x1) / 2; // x coord => center of border
          bboxGroup
            .append("polygon")
            .attr(
              "points",
              `${cx - a / 2}, ${y1} ${cx + a / 2}, ${y1} ${cx}, ${y1 - h}`
            )
            .attr("stroke", "#ffffff")
            .attr("stroke-width", "1px")
            .attr("stroke-opacity", 1)
            .attr("fill", "white")
            .attr("fill-opacity", 1)
            .attr("stroke-linejoin", "round");

          // add North (N) to the triangle
          bboxGroup
            .append("text")
            .attr("x", cx)
            .attr("y", y1 - h / 3)
            .attr("font-size", a / 2)
            .attr("text-anchor", "middle")
            .attr("dominant-baseline", "central")
            .html("N");

          // set flags to enable controls
          vm.cylinders.selection.border.exists = true;
        }
      }
    },
  },
};
</script>

<style>
#map-top-controls-container {
  height: 64px;
}

.x-gridlines line,
.y-gridlines line {
  stroke: #ccc;
}

.x-gridlines .tick,
.y-gridlines .tick {
  opacity: 0.7;
}

.x.axis .tick text,
.y.axis .tick text {
  font-size: 1.25vmin;
}

.x.axis.title text,
.y.axis.title text {
  font-size: 2.25vmin;
  font-weight: 800 !important;
}

.bound-box {
  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  /* Similar syntax to box-shadow */
}

.tooltip {
  position: absolute;
  text-align: center;
  width: 250px;
  height: 3.75em;
  padding: 2px;
  font: 12px sans-serif;
  background: #ccc;
  border: 0px;
  border-radius: 8px;
  pointer-events: none;
}
</style>
