<template>
  <v-container fluid>
    <!-- TOP -->
    <v-row id="interp-top-controls-container">
      <!-- TOP-LEFT -->
      <v-col cols="11">
        <v-row>
          <!-- BUTTON ARRAY -->
          <v-col cols="4" class="pr-1">
            <!-- SHAPE SELECT -->
            <v-btn
              icon
              small
              color="primary"
              @click="shape = shape == 'Prism' ? 'Cylinder' : 'Prism'"
            >
              <v-icon v-if="shape == 'Prism'">mdi-cube</v-icon>
              <v-icon class="cylinder-icon" v-if="shape == 'Cylinder'"
                >$cylinder</v-icon
              >
            </v-btn>

            <!-- HEIGHT TOGGLE -->
            <v-btn
              icon
              small
              :color="height == shapeSize ? 'primary' : 'default'"
              @click="height = height == shapeSize ? 1 : shapeSize"
            >
              <v-icon>mdi-arrow-expand-vertical</v-icon>
            </v-btn>

            <!-- INTERPOLATION TOGGLE -->
            <v-btn
              icon
              small
              :color="showInterp ? 'primary' : 'default'"
              @click="showInterp = !showInterp"
            >
              <v-icon>mdi-eye</v-icon>
            </v-btn>

            <!-- ELEVATION TOGGLE -->
            <v-btn disabled icon small>
              <v-icon>mdi-elevation-rise</v-icon>
            </v-btn>

            <!-- RESET CAMERA -->
            <v-btn icon small @click="resetCamera">
              <v-icon>mdi-camera-retake</v-icon>
            </v-btn>
          </v-col>

          <!-- COLOR MAP SELECT -->
          <v-col cols="4" class="px-1">
            <v-select
              outlined
              dense
              hide-details
              label="Color Map"
              v-model="cmap"
              :menu-props="{ bottom: true, offsetY: true }"
              :items="Object.keys(cmaps)"
            ></v-select>
          </v-col>

          <!-- VARIABLE SELECT -->
          <v-col cols="4" class="pl-1">
            <v-select
              outlined
              dense
              hide-details
              label="Value"
              v-model="variable"
              :menu-props="{ bottom: true, offsetY: true }"
              :items="variables"
            ></v-select>
          </v-col>
        </v-row>
      </v-col>

      <!-- TOP-RIGHT -->
      <v-col cols="1">
        <v-icon>mdi-camera-control</v-icon>
      </v-col>
    </v-row>

    <!-- MIDDLE -->
    <v-row>
      <!-- MIDDLE-LEFT -->
      <v-col cols="11" id="canvas-container" class="py-0">
        <v-container fluid>
          <v-row style="height: 48px">
            <v-col cols="12" class="pa-0">
              <!-- VALUE SLIDER -->
              <v-range-slider
                v-if="variableFilter"
                dense
                hide-details
                class="slider-border px-2"
                v-model="variableFilter"
                :min="bounds[variable][0]"
                :max="bounds[variable][1]"
                :step="variableStep"
                @input="clean"
              >
                <template v-slot:prepend>
                  <v-container fluid class="pa-0">
                    <v-row no-gutters class="flex-nowrap">
                      <label
                        class="v-label theme--light pr-2"
                        style="height: 1rem; align-self: center"
                      >
                        Values
                      </label>
                      <label
                        class="v-label theme--light text-right"
                        style="height: 1rem; align-self: center; width: 64px"
                      >
                        {{
                          Math.round(
                            (variableFilter[0] + Number.EPSILON) *
                              (1 / variableStep)
                          ) /
                          (1 / variableStep)
                        }}
                      </label>
                    </v-row>
                  </v-container>
                </template>
                <template v-slot:append>
                  <label
                    class="v-label theme--light text-left"
                    style="height: 1rem; align-self: center; width: 64px"
                  >
                    {{
                      Math.round(
                        (variableFilter[1] + Number.EPSILON) *
                          (1 / variableStep)
                      ) /
                      (1 / variableStep)
                    }}
                  </label>
                </template>
              </v-range-slider>

              <!-- NO FILTER AVAILABLE MESSAGE -->
              <span v-else>No data found for this variable.</span>
            </v-col>
          </v-row>
        </v-container>

        <!-- CANVAS -->
        <canvas id="c"></canvas>
      </v-col>

      <!-- MIDDLE-RIGHT -->
      <v-col cols="1" id="right-controls-container" class="slider-border pa-0">
        <!-- DEPTH SLIDER -->
        <div class="depth-slider-label">
          <label class="v-label theme--light">Dep</label>
        </div>
        <v-btn
          icon
          x-small
          style="width: 100%"
          :color="clipPlanes.y.global ? 'primary' : 'default'"
          @click="toggleYClipGlobal"
        >
          <v-icon>mdi-earth</v-icon>
        </v-btn>
        <v-btn
          icon
          x-small
          style="width: 100%"
          :color="yClipPlaneHelperVisible ? 'primary' : 'default'"
          @click="toggleYClipHelper"
        >
          <v-icon>mdi-grid</v-icon>
        </v-btn>
        <v-btn
          icon
          x-small
          style="width: 100%"
          :color="Math.sign(clipPlanes.y.normal.y) > 0 ? 'primary' : 'default'"
          @click="toggleYClipNormal"
        >
          <v-icon>mdi-flip-horizontal</v-icon>
        </v-btn>
        <v-slider
          hide-details
          vertical
          thumb-label
          class="depth-slider px-3 py-0"
          v-model="clipPlanes.y.value"
          :thumb-size="24"
          :min="clipPlanes.y.bounds[1]"
          :max="clipPlanes.y.bounds[0]"
          :step="clipPlanes.y.step"
          @input="updateObjects"
        ></v-slider>
      </v-col>
    </v-row>

    <!-- BOTTOM -->
    <v-row id="interp-bottom-controls-container">
      <!-- BOTTOM-LEFT -->
      <v-col cols="11">
        <v-row>
          <!-- LAT SLIDER -->
          <v-col cols="6" class="pr-1">
            <v-slider
              dense
              hide-details
              thumb-label
              class="slider-border px-2"
              v-model="clipPlanes.z.value"
              :thumb-size="24"
              :min="clipPlanes.z.bounds[0]"
              :max="clipPlanes.z.bounds[1]"
              :step="clipPlanes.z.step"
              @input="updateObjects"
            >
              <template v-slot:prepend>
                <v-container fluid class="pa-0">
                  <v-row no-gutters class="flex-nowrap">
                    <label
                      class="v-label theme--light pr-2"
                      style="height: 1rem; align-self: center"
                    >
                      Lat
                    </label>
                    <v-btn
                      icon
                      x-small
                      class="mx-1"
                      :color="clipPlanes.z.global ? 'primary' : 'default'"
                      @click="toggleZClipGlobal"
                    >
                      <v-icon>mdi-earth</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      x-small
                      class="mx-1"
                      :color="zClipPlaneHelperVisible ? 'primary' : 'default'"
                      @click="toggleZClipHelper"
                    >
                      <v-icon>mdi-grid</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      x-small
                      class="mx-1"
                      :color="
                        Math.sign(clipPlanes.z.normal.z) > 0
                          ? 'primary'
                          : 'default'
                      "
                      @click="toggleZClipNormal"
                    >
                      <v-icon>mdi-flip-horizontal</v-icon>
                    </v-btn>
                  </v-row>
                </v-container>
              </template>
            </v-slider>
          </v-col>

          <!-- LON SLIDER -->
          <v-col cols="6" class="pl-1">
            <v-slider
              dense
              hide-details
              thumb-label
              class="slider-border px-2"
              v-model="clipPlanes.x.value"
              :thumb-size="24"
              :min="clipPlanes.x.bounds[0]"
              :max="clipPlanes.x.bounds[1]"
              :step="clipPlanes.x.step"
              @input="updateObjects"
            >
              <template v-slot:prepend>
                <v-container fluid class="pa-0">
                  <v-row no-gutters class="flex-nowrap">
                    <label
                      class="v-label theme--light pr-2"
                      style="height: 1rem; align-self: center"
                    >
                      Lon
                    </label>
                    <v-btn
                      icon
                      x-small
                      class="mx-1"
                      :color="clipPlanes.x.global ? 'primary' : 'default'"
                      @click="toggleXClipGlobal"
                    >
                      <v-icon>mdi-earth</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      x-small
                      class="mx-1"
                      :color="xClipPlaneHelperVisible ? 'primary' : 'default'"
                      @click="toggleXClipHelper"
                    >
                      <v-icon>mdi-grid</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      x-small
                      class="mx-1"
                      :color="
                        -Math.sign(clipPlanes.x.normal.x) > 0
                          ? 'primary'
                          : 'default'
                      "
                      @click="toggleXClipNormal"
                    >
                      <v-icon>mdi-flip-horizontal</v-icon>
                    </v-btn>
                  </v-row>
                </v-container>
              </template>
            </v-slider>
          </v-col>
        </v-row>
      </v-col>

      <!-- BOTTOM-RIGHT -->
      <v-col cols="1">
        <v-icon @click="resetView">mdi-restart</v-icon>
      </v-col>
    </v-row>

    <!-- VIEW SLIDERS -->
    <v-row justify="center" style="display: none">
      <v-col cols="12">
        <div id="view-sliders">
          <strong
            style="grid-column: 1 / 4; place-self: center left; color: #c2b280"
            >Grid</strong
          >
          <div style="grid-column: 4; place-self: center right">
            <input type="checkbox" id="grid-elev-toggle-checkbox" />
            <label for="grid-elev-toggle-checkbox">Show</label>
          </div>
          <span
            style="grid-column: 1; text-align: right"
            id="grid-elev-output"
          ></span>
          <input
            style="grid-column: 2 / 5"
            type="range"
            min="-30"
            max="0"
            id="grid-elev-input"
          />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import $ from "jquery";
import * as d3 from "d3";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import throttle from "lodash/throttle";

function countDecimals(value) {
  if (Math.floor(value) === value) return 0;
  let result;
  let str = value.toString();
  if (str.indexOf(".") !== -1 && str.indexOf("-") !== -1) {
    result = str.split("-")[1] || 0;
  } else if (str.indexOf(".") !== -1) {
    result = str.split(".")[1].length || 0;
  } else {
    result = str.split("-")[1] || 0;
  }
  return result;
}

class clipPlane {
  constructor(normal, constant) {
    this.plane = new THREE.Plane(normal, constant);
    this.value = 0;
    this.step = 1;
    this.bounds = [0, 0];
    this.global = false;
    this.helper = undefined;
  }
}

Object.defineProperty(clipPlane.prototype, "normal", {
  get() {
    return this.plane.normal;
  },
  set(val) {
    this.plane.set(val, this.plane.constant);
  },
});

Object.defineProperty(clipPlane.prototype, "constant", {
  get() {
    return this.plane.constant;
  },
  set(val) {
    this.plane.set(this.plane.normal, val);
  },
});

export default {
  name: "InterpView",

  props: {
    interpData: {
      type: Array,
      default: () => [],
    },
    resolution: {
      type: Number,
      default: () => 0,
    },
    variables: {
      type: Array,
      default: () => [],
    },
  },

  data: () => ({
    // threejs variables and helpers
    canvas: undefined,
    renderer: undefined,
    scene: undefined,
    controls: undefined,
    camera: undefined,
    mesh: undefined,
    gridHelper: undefined,
    axesHelper: undefined,
    frustumHelper: undefined,
    light1: undefined,
    light2: undefined,
    matrix: new THREE.Matrix4(), // dummy matrix helper
    color: new THREE.Color(), // dummy color helper
    clipPlanes: {
      x: new clipPlane(new THREE.Vector3(1, 0, 0), 0),
      y: new clipPlane(new THREE.Vector3(0, -1, 0), 0),
      z: new clipPlane(new THREE.Vector3(0, 0, -1), 0),
    },

    // input parameters
    bounds: {},
    height: 1, // the height of the geometry, in cm
    variable: null,
    variableFilter: null,
    variableStep: null,
    shape: "Prism",
    shapes: ["Prism", "Cylinder"],
    shapeSize: null,
    cmap: "Viridis",
    cmaps: {
      Viridis: d3.interpolateViridis,
      Cividis: d3.interpolateCividis,
      Greys: d3.interpolateGreys,
      Inferno: d3.interpolateInferno,
      Plasma: d3.interpolatePlasma,
      Magma: d3.interpolateMagma,
    },

    // flags
    showInterp: true, // whether to show interpolated values
    renderRequested: false, // flag for whether to re-render or not
    gridHelperVisible: false, // hide grid helper by default
    axesHelperVisible: false, // show axes helper by default
    frustumHelperVisible: false, // hide frustum helper by default
    xClipPlaneHelperVisible: false, // hide clip plane helpers by default
    yClipPlaneHelperVisible: false,
    zClipPlaneHelperVisible: false,
    hideCameraSliders: false, // hide sliders by default
    hideViewSliders: false,
  }),

  mounted: function () {
    let vm = this;

    // set the event listeners
    $(window).on("resize", vm.requestRenderIfNotRequested);
    $("#grid-elev-input").on("input", vm.updateObjects);
    $(`#grid-elev-toggle-checkbox`).on("click", vm.updateHelpers);

    // set initial view inputs and params
    $("#grid-elev-input").val(-30);
    $("#grid-elev-output").html(`${$("#grid-elev-input").val()}cm`);

    // start the show!
    vm.variable = vm.variables[0]; // set initial variable
    vm.init();
    vm.parseData(vm.interpData);
    vm.addMeshes();
    vm.requestRenderIfNotRequested();
  },

  watch: {
    interpData: function (newData) {
      this.variable = this.variables[0]; // set initial variable
      this.parseData(newData);
      this.clean();
    },
    variable: function (newVariable) {
      let vm = this;
      // update variable filter to new variable's bounds, if possible
      const lb = vm.bounds[newVariable][0];
      const rb = vm.bounds[newVariable][1];
      if (lb == 0 && rb == 0) {
        vm.variableFilter = null; // assume variable cannot be plotted, no filtering
        vm.variableStep = null;
      } else {
        vm.variableFilter = [lb, rb]; // set filter to variables bounds
        vm.variableStep = 10 ** -Math.max(countDecimals(lb), countDecimals(rb)); // step is based on decimals
      }
      vm.clean();
    },
    shape: function (newShape) {
      let vm = this;
      // when shape changes, need to recompute shape size and height
      switch (newShape) {
        case "Cylinder": {
          vm.shapeSize = 7; // 7 cm diameter
          break;
        }
        case "Prism": {
          vm.shapeSize = vm.resolution; // based on resolution
          break;
        }
      }
      if (vm.height > 1) vm.height = vm.shapeSize; // update height
      vm.clean(); // re-draw
    },
    cmap: function () {
      this.clean();
    },
    height: function () {
      let vm = this;
      // when height changes, need to re-compute the y clip plane constant
      const ySign = Math.sign(vm.clipPlanes.y.normal.y);
      const yInput = -vm.clipPlanes.y.value;
      if (yInput == -vm.clipPlanes.y.bounds[0]) {
        vm.clipPlanes.y.constant = ySign * (yInput * vm.height - 0.01);
      } else if (yInput == -vm.clipPlanes.y.bounds[1]) {
        vm.clipPlanes.y.constant = ySign * (yInput * vm.height + 0.01);
      } else {
        vm.clipPlanes.y.constant = ySign * yInput * vm.height + 0.01;
      }
      // then re-draw
      vm.clean();
    },
    showInterp: function () {
      this.clean();
    },
  },

  methods: {
    init() {
      let vm = this;
      vm.canvas = $("#c")[0]; // canvas

      // renderer
      vm.renderer = new THREE.WebGLRenderer({ canvas: vm.canvas });
      vm.renderer.localClippingEnabled = true;

      // scene
      vm.scene = new THREE.Scene();
      vm.scene.background = new THREE.Color(0xeeeeee);

      // camera
      const fov = 50; // field of view
      const aspect = 1; // aspect ratio; the canvas default is 2
      const near = 0.1; // near clipping plane
      const far = 2000; // far clipping plane
      vm.camera = new THREE.PerspectiveCamera(fov, aspect, near, far);

      // controls
      vm.controls = new OrbitControls(vm.camera, vm.canvas);
      vm.controls.listenToKeyEvents(window);
      vm.controls.object.position.set(-100, 100, 100);
      vm.controls.target = new THREE.Vector3(80, 0, -70);
      vm.controls.enableDamping = true;
      vm.controls.dampingFactor = 0.05;
      vm.controls.screenSpacePanning = false;
      vm.controls.minDistance = 0;
      vm.controls.maxDistance = 500;
      vm.controls.maxPolarAngle = Math.PI;
      // vm.controls.mouseButtons = {
      //   LEFT: THREE.MOUSE.PAN,
      //   MIDDLE: THREE.MOUSE.DOLLY,
      //   RIGHT: THREE.MOUSE.ROTATE,
      // };
      $(vm.controls).on("change", vm.requestRenderIfNotRequested);

      // lights
      vm.light1 = new THREE.HemisphereLight(0xffffff, 0x000088);
      vm.light1.position.set(-1, 1.5, 1);
      vm.scene.add(vm.light1);
      vm.light2 = new THREE.HemisphereLight(0xffffff, 0x880000, 0.5);
      vm.light2.position.set(-1, -1.5, -1);
      vm.scene.add(vm.light2);

      // frustum helper
      vm.frustumHelper = new THREE.CameraHelper(vm.camera);
      vm.frustumHelper.name = "frustumHelper";
      vm.frustumHelper.visible = vm.frustumHelperVisible;
      vm.scene.add(vm.frustumHelper);

      // grid helper
      vm.gridHelper = new THREE.GridHelper(1000, 1000, "black", "#C2B280");
      vm.gridHelper.name = "gridHelper";
      vm.gridHelper.visible = vm.gridHelperVisible;
      vm.scene.add(vm.gridHelper);

      // axis helper
      vm.axesHelper = new THREE.AxesHelper(30);
      vm.axesHelper.name = "axesHelper";
      vm.axesHelper.visible = vm.axesHelperVisible;
      vm.scene.add(vm.axesHelper);

      // plane helpers
      vm.clipPlanes.x.helper = new THREE.PlaneHelper(
        vm.clipPlanes.x.plane,
        15,
        0xff0000
      );
      vm.clipPlanes.x.helper.name = "xClipPlaneHelper";
      vm.clipPlanes.x.helper.visible = vm.xClipPlaneHelperVisible;
      vm.clipPlanes.y.helper = new THREE.PlaneHelper(
        vm.clipPlanes.y.plane,
        15,
        0x00ff00
      );
      vm.clipPlanes.y.helper.name = "yClipPlaneHelper";
      vm.clipPlanes.y.helper.visible = vm.yClipPlaneHelperVisible;
      vm.clipPlanes.z.helper = new THREE.PlaneHelper(
        vm.clipPlanes.z.plane,
        15,
        0x0000ff
      );
      vm.clipPlanes.z.helper.name = "zClipPlaneHelper";
      vm.clipPlanes.z.helper.visible = vm.zClipPlaneHelperVisible;
      vm.scene.add(vm.clipPlanes.x.helper);
      vm.scene.add(vm.clipPlanes.y.helper);
      vm.scene.add(vm.clipPlanes.z.helper);
    },

    parseData(newData) {
      let vm = this;

      // create new boundaries data object
      vm.bounds = Object.assign(
        {
          x: [0, 0],
          y: [0, 0],
          z: [0, 0],
          d: [0, 0],
        },
        Object.fromEntries(vm.variables.map((x) => [x, [0, 0]]))
      );

      // set initial x,y,z,dist bounds using first point
      // these columns are always fully populated, so taking the first
      // value should be fine
      let i = 0;
      let d = newData[i];
      vm.bounds.x[0] = parseFloat(d.X);
      vm.bounds.x[1] = parseFloat(d.X);
      vm.bounds.y[0] = parseFloat(d.Y);
      vm.bounds.y[1] = parseFloat(d.Y);
      vm.bounds.z[0] = parseFloat(d.Z);
      vm.bounds.z[1] = parseFloat(d.Z);
      vm.bounds.d[0] = parseFloat(d["dist_to_nearest"]);
      vm.bounds.d[1] = parseFloat(d["dist_to_nearest"]);

      // some datasets (like linear) have missing values for variables
      // walk through until you can set initial bounds on each variable
      let varSet = vm.variables.map(() => false);
      let stopIter = true;
      for (let j = 0; j < newData.length; j++) {
        for (let k = 0; k < vm.variables.length; k++) {
          if (!varSet[k]) {
            const v = parseFloat(newData[j][vm.variables[k]]);
            if (!Number.isNaN(v)) {
              vm.bounds[vm.variables[k]][0] = v;
              vm.bounds[vm.variables[k]][1] = v;
              varSet[k] = true;
            } else {
              stopIter = false;
            }
          }
        }
        if (stopIter) break;
      }

      // walk through the rest of the points
      for (i = 1; i < newData.length; i++) {
        d = newData[i];
        vm.bounds.x[0] = Math.min(vm.bounds.x[0], parseFloat(d.X));
        vm.bounds.x[1] = Math.max(vm.bounds.x[1], parseFloat(d.X));
        vm.bounds.y[0] = Math.min(vm.bounds.y[0], parseFloat(d.Y));
        vm.bounds.y[1] = Math.max(vm.bounds.y[1], parseFloat(d.Y));
        vm.bounds.z[0] = Math.min(vm.bounds.z[0], parseFloat(d.Z));
        vm.bounds.z[1] = Math.max(vm.bounds.z[1], parseFloat(d.Z));
        vm.bounds.d[0] = Math.min(
          vm.bounds.d[0],
          parseFloat(d["dist_to_nearest"])
        );
        vm.bounds.d[1] = Math.max(
          vm.bounds.d[1],
          parseFloat(d["dist_to_nearest"])
        );
        for (let k = 0; k < vm.variables.length; k++) {
          const v = parseFloat(d[vm.variables[k]]); // returns either "Number" or "NaN"
          if (!Number.isNaN(v)) {
            vm.bounds[vm.variables[k]][0] = Math.min(
              vm.bounds[vm.variables[k]][0],
              v
            );
            vm.bounds[vm.variables[k]][1] = Math.max(
              vm.bounds[vm.variables[k]][1],
              v
            );
          }
        }
      }

      // set variable filter to current variable's bounds, if possible
      const lb = vm.bounds[vm.variable][0];
      const rb = vm.bounds[vm.variable][1];
      if (lb == 0 && rb == 0) {
        vm.variableFilter = null; // assume variable cannot be plotted, no filtering
        vm.variableStep = null;
      } else {
        vm.variableFilter = [lb, rb]; // set filter to variables bounds
        vm.variableStep = 10 ** -Math.max(countDecimals(lb), countDecimals(rb)); // step is based on decimals
      }

      // set clipping plane bounds
      vm.clipPlanes.x.bounds = [vm.bounds.x[0], vm.bounds.x[1] + vm.resolution];
      vm.clipPlanes.y.bounds = [-vm.bounds.z[0], -(vm.bounds.z[1] + 1)];
      vm.clipPlanes.z.bounds = [vm.bounds.y[0], vm.bounds.y[1] + vm.resolution];

      // set constants, applying small correction to start
      vm.clipPlanes.x.constant =
        -Math.sign(vm.clipPlanes.x.normal.x) * vm.clipPlanes.x.bounds[0] + 0.01;
      vm.clipPlanes.y.constant =
        Math.sign(vm.clipPlanes.y.normal.y) * -vm.clipPlanes.y.bounds[0] + 0.01;
      vm.clipPlanes.z.constant =
        Math.sign(vm.clipPlanes.z.normal.z) * vm.clipPlanes.z.bounds[0] + 0.01;

      // set clipping plane value and step
      vm.clipPlanes.x.value = vm.clipPlanes.x.bounds[0];
      vm.clipPlanes.x.step = vm.resolution;
      vm.clipPlanes.y.value = vm.clipPlanes.y.bounds[0];
      vm.clipPlanes.y.step = 1;
      vm.clipPlanes.z.value = vm.clipPlanes.z.bounds[0];
      vm.clipPlanes.z.step = vm.resolution;
    },

    addMeshes() {
      let vm = this;

      // only attempt to generate the mesh if there is an active filter set,
      // since the filter is null if the variable to be plotted has equal bounds

      if (vm.variableFilter) {
        // build the geometry
        let geometry;
        switch (vm.shape) {
          case "Cylinder": {
            vm.shapeSize = 7; // 7 cm diameter
            const radiusTop = vm.shapeSize / 2; // top radius
            const radiusBottom = vm.shapeSize / 2; // bottom radius
            const radialEdges = 12;
            geometry = new THREE.CylinderGeometry(
              radiusTop,
              radiusBottom,
              vm.height,
              radialEdges
            );
            break;
          }
          case "Prism": {
            vm.shapeSize = vm.resolution; // based on resolution
            const width = vm.shapeSize; // x axis, in cm
            const depth = vm.shapeSize; // z axis, in cm
            geometry = new THREE.BoxGeometry(width, vm.height, depth);
            break;
          }
        }
        // build the material
        const material = new THREE.MeshMatcapMaterial({
          side: THREE.DoubleSide,
          clippingPlanes: [
            vm.clipPlanes.x.plane,
            vm.clipPlanes.y.plane,
            vm.clipPlanes.z.plane,
          ],
          clipIntersection: true,
        });
        // create a new instanced mesh
        vm.mesh = new THREE.InstancedMesh(
          geometry,
          material,
          vm.interpData.length
        );
        // apply transformations at every vertex to get final shape
        const colorScale = vm.cmaps[vm.cmap]; // get interpolator
        const varBounds = vm.bounds[vm.variable]; // get var bounds
        for (let i = 0; i < vm.interpData.length; i++) {
          const dataPoint = vm.interpData[i];
          const o = parseFloat(dataPoint["observed"]);
          if (vm.showInterp) {
            const v = parseFloat(dataPoint[vm.variable]);
            if (
              !Number.isNaN(v) &&
              v >= vm.variableFilter[0] &&
              v <= vm.variableFilter[1]
            ) {
              // transform
              const x = parseFloat(dataPoint.X);
              const y = parseFloat(dataPoint.Y);
              const z = parseFloat(dataPoint.Z);
              // const d = parseFloat(dataPoint["dist_to_nearest"]);
              // const d_norm = (d - vm.bounds.d[1]) / (vm.bounds.d[0] - vm.bounds.d[1]);
              // vm.matrix.makeScale(1, d_norm, 1);
              vm.matrix.setPosition(
                x + vm.resolution / 2,
                -vm.height * (z + 0.5),
                -(y + vm.resolution / 2)
              );
              vm.mesh.setMatrixAt(i, vm.matrix);
              // color
              let v_norm = (v - varBounds[0]) / (varBounds[1] - varBounds[0]);
              if (vm.cmap == "Greys") v_norm = 1 - v_norm; // invert scale
              vm.mesh.setColorAt(i, vm.color.set(colorScale(v_norm)));
              // border
              if (o) {
                const edges = new THREE.EdgesGeometry(geometry);
                const line = new THREE.LineSegments(
                  edges,
                  new THREE.LineBasicMaterial({ color: 0xffffff })
                );
                line.geometry.applyMatrix4(vm.matrix);
                vm.scene.add(line);
              }
            }
          } else if (!vm.showInterp && o) {
            const v = parseFloat(dataPoint[vm.variable]);
            if (
              !Number.isNaN(v) &&
              v >= vm.variableFilter[0] &&
              v <= vm.variableFilter[1]
            ) {
              // transform
              const x = parseFloat(dataPoint.X);
              const y = parseFloat(dataPoint.Y);
              const z = parseFloat(dataPoint.Z);
              // const d = parseFloat(dataPoint["dist_to_nearest"]);
              // const d_norm = (d - vm.bounds.d[1]) / (vm.bounds.d[0] - vm.bounds.d[1]);
              // vm.matrix.makeScale(1, d_norm, 1);
              vm.matrix.setPosition(
                x + vm.resolution / 2,
                -vm.height * (z + 0.5),
                -(y + vm.resolution / 2)
              );
              vm.mesh.setMatrixAt(i, vm.matrix);
              // color
              let v_norm = (v - varBounds[0]) / (varBounds[1] - varBounds[0]);
              if (vm.cmap == "Greys") v_norm = 1 - v_norm; // invert scale
              vm.mesh.setColorAt(i, vm.color.set(colorScale(v_norm)));
              // border
              // const edges = new THREE.EdgesGeometry(geometry);
              // const line = new THREE.LineSegments(edges, new THREE.LineBasicMaterial({ color: 0xffffff }));
              // line.geometry.applyMatrix4(vm.matrix);
              // vm.scene.add(line);
            }
          }
        }
        // add mesh to the scene
        vm.scene.add(vm.mesh);
      }

      // add helper bounding box to the scene
      const bboxWidth = vm.clipPlanes.x.bounds[1]; // x-axis
      const bboxHeight = 10; // y-axis
      const bboxDepth = vm.clipPlanes.z.bounds[1]; // z-axis
      const splines = {
        top: new THREE.CatmullRomCurve3([
          new THREE.Vector3(-10, bboxHeight, -(bboxDepth + 10)),
          new THREE.Vector3(bboxWidth + 10, bboxHeight, -(bboxDepth + 10)),
        ]),
        right: new THREE.CatmullRomCurve3([
          new THREE.Vector3(bboxWidth + 10, bboxHeight, -(bboxDepth + 10)),
          new THREE.Vector3(bboxWidth + 10, bboxHeight, 10),
        ]),
        bottom: new THREE.CatmullRomCurve3([
          new THREE.Vector3(bboxWidth + 10, bboxHeight, 10),
          new THREE.Vector3(-10, bboxHeight, 10),
        ]),
        left: new THREE.CatmullRomCurve3([
          new THREE.Vector3(-10, bboxHeight, 10),
          new THREE.Vector3(-10, bboxHeight, -(bboxDepth + 10)),
        ]),
      };
      Object.keys(splines).forEach((key) => {
        const params = {
          path: splines[key],
          tubularSegments: 100,
          radius: 1,
          radialSegments: 20,
          closed: true,
        };
        const tubeGeometry = new THREE.TubeGeometry(
          params.path,
          params.tubularSegments,
          params.radius,
          params.radialSegments,
          params.closed
        );
        const tubeMaterial = new THREE.MeshMatcapMaterial({ color: 0xbbbbbb });
        const tubeMesh = new THREE.Mesh(tubeGeometry, tubeMaterial);
        vm.scene.add(tubeMesh);
      });

      // create unicorn horn
      const a = Math.max(0.1 * bboxWidth, 16); // side length => MAX 10% of width or 16px
      const h = (Math.sqrt(3) / 2) * a; // height of equilateral triangle with sides a
      const hornShape = new THREE.Shape()
        .moveTo(-a / 2, -h / 2)
        .lineTo(a / 2, -h / 2)
        .lineTo(0, h / 2)
        .lineTo(-a / 2, -h / 2); // close path
      const extrudeSettings = {
        steps: 10,
        depth: 1,
        bevelEnabled: false,
      };
      const hornGeometry = new THREE.ExtrudeGeometry(
        hornShape,
        extrudeSettings
      );
      const hornMaterial = new THREE.MeshMatcapMaterial({ color: 0xbbbbbb });
      const hornMesh = new THREE.Mesh(hornGeometry, hornMaterial);
      hornMesh.rotation.set(-Math.PI / 2, 0, 0);
      hornMesh.position.set(
        vm.clipPlanes.x.bounds[1] / 2,
        bboxHeight,
        -(vm.clipPlanes.z.bounds[1] + h / 2 + 10)
      );
      vm.scene.add(hornMesh);
    },

    toggleXClipGlobal() {
      this.clipPlanes.x.global = !this.clipPlanes.x.global;
      this.updateObjects();
    },

    toggleXClipHelper() {
      this.xClipPlaneHelperVisible = !this.xClipPlaneHelperVisible;
      this.clipPlanes.x.helper.visible = this.xClipPlaneHelperVisible; // set state
      this.updateObjects();
    },

    toggleXClipNormal() {
      this.clipPlanes.x.normal = this.clipPlanes.x.normal.negate();
      this.updateObjects();
    },

    toggleYClipGlobal() {
      this.clipPlanes.y.global = !this.clipPlanes.y.global;
      this.updateObjects();
    },

    toggleYClipHelper() {
      this.yClipPlaneHelperVisible = !this.yClipPlaneHelperVisible;
      this.clipPlanes.y.helper.visible = this.yClipPlaneHelperVisible; // set state
      this.updateObjects();
    },

    toggleYClipNormal() {
      this.clipPlanes.y.normal = this.clipPlanes.y.normal.negate();
      this.updateObjects();
    },

    toggleZClipGlobal() {
      this.clipPlanes.z.global = !this.clipPlanes.z.global;
      this.updateObjects();
    },

    toggleZClipHelper() {
      this.zClipPlaneHelperVisible = !this.zClipPlaneHelperVisible;
      this.clipPlanes.z.helper.visible = this.zClipPlaneHelperVisible; // set state
      this.updateObjects();
    },

    toggleZClipNormal() {
      this.clipPlanes.z.normal = this.clipPlanes.z.normal.negate();
      this.updateObjects();
    },

    updateHelpers(e) {
      let vm = this;
      const el = $(e.target);
      const id = el.attr("id");
      switch (id) {
        case "grid-elev-toggle-checkbox":
          vm.gridHelperVisible = !vm.gridHelperVisible; // toggle
          vm.gridHelper.visible = vm.gridHelperVisible; // set state
          if (vm.gridHelperVisible) {
            vm.updateObjects();
          } else {
            vm.requestRenderIfNotRequested();
          }
          break;
      }
    },

    updateObjects() {
      let vm = this;

      // adjust gridHelper
      if (vm.gridHelperVisible)
        vm.gridHelper.position.y = 30 + parseFloat($("#grid-elev-input").val());

      // update helper visible state
      vm.clipPlanes.y.helper.visible = vm.yClipPlaneHelperVisible; // set state
      vm.clipPlanes.z.helper.visible = vm.zClipPlaneHelperVisible; // set state

      // adjust clip planes, correcting at the boundaries to avoid glitchy textures
      // this took forever to figure out... I hope someone appreciates this...

      // x axis
      const xSign = Math.sign(vm.clipPlanes.x.normal.x);
      const xInput = vm.clipPlanes.x.value;
      if (xInput == vm.clipPlanes.x.bounds[0]) {
        vm.clipPlanes.x.constant = -xSign * (xInput - 0.01);
      } else if (xInput == vm.clipPlanes.x.bounds[1]) {
        vm.clipPlanes.x.constant = -xSign * (xInput + 0.01);
      } else {
        vm.clipPlanes.x.constant = -xSign * xInput + 0.01;
      }

      // y axis
      const ySign = Math.sign(vm.clipPlanes.y.normal.y);
      const yInput = -vm.clipPlanes.y.value;
      if (yInput == -vm.clipPlanes.y.bounds[0]) {
        vm.clipPlanes.y.constant = ySign * (yInput * vm.height - 0.01);
      } else if (yInput == -vm.clipPlanes.y.bounds[1]) {
        vm.clipPlanes.y.constant = ySign * (yInput * vm.height + 0.01);
      } else {
        vm.clipPlanes.y.constant = ySign * yInput * vm.height + 0.01;
      }

      // z axis
      const zSign = Math.sign(vm.clipPlanes.z.normal.z);
      const zInput = vm.clipPlanes.z.value;
      if (zInput == vm.clipPlanes.z.bounds[0]) {
        vm.clipPlanes.z.constant = zSign * (zInput - 0.01);
      } else if (zInput == vm.clipPlanes.z.bounds[1]) {
        vm.clipPlanes.z.constant = zSign * (zInput + 0.01);
      } else {
        vm.clipPlanes.z.constant = zSign * zInput + 0.01;
      }

      // update outputs
      $("#grid-elev-output").html(`${$("#grid-elev-input").val()}cm`);

      // set global clip planes, if any are toggled
      let globalPlanes = [];
      if (vm.clipPlanes.x.global) globalPlanes.push(vm.clipPlanes.x.plane);
      if (vm.clipPlanes.y.global) globalPlanes.push(vm.clipPlanes.y.plane);
      if (vm.clipPlanes.z.global) globalPlanes.push(vm.clipPlanes.z.plane);
      vm.renderer.clippingPlanes =
        globalPlanes.length > 0 ? globalPlanes : Object.freeze([]);

      // re-render
      vm.requestRenderIfNotRequested();
    },

    resetCamera() {
      let vm = this;
      // update inputs to be default value
      $("#fov-input").val(50);
      $("#near-input").val(0.1);
      $("#far-input").val(2000);
      // update orbit controls camera
      vm.controls.object.position.set(-100, 200, 100);
      vm.controls.target = new THREE.Vector3(80, 30, -70);
      // request new render
      vm.camera.updateProjectionMatrix();
      vm.requestRenderIfNotRequested();
    },

    resetView() {
      let vm = this;

      // reset interp
      const showInterpChanged = vm.showInterp !== true; // currently different from default
      vm.showInterp = true; // reset to default value

      // reset height
      const heightChanged = vm.height !== 1; // currently different from default
      vm.height = 1; // default value, in cm

      // reset variable filter, if possible
      const lb = vm.bounds[vm.variable][0];
      const rb = vm.bounds[vm.variable][1];
      if (lb == 0 && rb == 0) {
        vm.variableFilter = null; // assume variable cannot be plotted, no filtering
        vm.variableStep = null;
      } else {
        vm.variableFilter = [lb, rb]; // set filter to variables bounds
        vm.variableStep = 10 ** -Math.max(countDecimals(lb), countDecimals(rb)); // step is based on decimals
      }

      // reset gridhelper
      vm.gridHelperVisible = false; // set flag
      vm.gridHelper.visible = false; // set state
      vm.gridHelper.position.y = 0; // set position
      $("#grid-elev-toggle-checkbox").prop("checked", false);
      $("#grid-elev-input").val(-30);
      $("#grid-elev-output").html(`${$("#grid-elev-input").val()}cm`);

      // reset global clip planes
      vm.clipPlanes.x.global = false;
      vm.clipPlanes.y.global = false;
      vm.clipPlanes.z.global = false;
      $("#x-clip-global-checkbox").prop("checked", false);
      $("#y-clip-global-checkbox").prop("checked", false);
      $("#z-clip-global-checkbox").prop("checked", false);
      vm.renderer.clippingPlanes = Object.freeze([]);

      // reset clipping plane normals and inputs
      vm.clipPlanes.x.normal = new THREE.Vector3(1, 0, 0);
      vm.clipPlanes.y.normal = new THREE.Vector3(0, -1, 0);
      vm.clipPlanes.z.normal = new THREE.Vector3(0, 0, -1);
      $("#x-clip-flip-checkbox").prop("checked", false);
      $("#y-clip-flip-checkbox").prop("checked", false);
      $("#z-clip-flip-checkbox").prop("checked", false);

      // reset clipping plane helpers
      vm.xClipPlaneHelperVisible = false; // set flag
      vm.clipPlanes.x.helper.visible = false; // set state
      $("#x-clip-helper-checkbox").prop("checked", false);
      vm.yClipPlaneHelperVisible = false; // set flag
      vm.clipPlanes.y.helper.visible = false; // set state
      $("#y-clip-helper-checkbox").prop("checked", false);
      vm.zClipPlaneHelperVisible = false; // set flag
      vm.clipPlanes.z.helper.visible = false; // set state
      $("#z-clip-helper-checkbox").prop("checked", false);

      // reset clipping plane constants
      vm.clipPlanes.x.constant =
        -Math.sign(vm.clipPlanes.x.normal.x) * vm.clipPlanes.x.bounds[0] + 0.01;
      vm.clipPlanes.y.constant =
        Math.sign(vm.clipPlanes.y.normal.y) * -vm.clipPlanes.y.bounds[0] + 0.01;
      vm.clipPlanes.z.constant =
        Math.sign(vm.clipPlanes.z.normal.z) * vm.clipPlanes.z.bounds[0] + 0.01;

      // set clipping plane values to min boundary to start
      vm.clipPlanes.x.value = vm.clipPlanes.x.bounds[0];
      vm.clipPlanes.y.value = vm.clipPlanes.y.bounds[0];
      vm.clipPlanes.z.value = vm.clipPlanes.z.bounds[0];

      // reset options
      if (showInterpChanged || heightChanged) {
        vm.clean(); // have to remove old stuff
      } else {
        vm.requestRenderIfNotRequested(); // only re-render
      }
    },

    clean: throttle(
      function () {
        let vm = this;

        // remove currently drawn meshes and lines
        const dontRemove = [
          "gridHelper",
          "axesHelper",
          "frustumHelper",
          "xClipPlaneHelper",
          "yClipPlaneHelper",
          "zClipPlaneHelper",
        ];
        const meshes = [];
        const lines = [];
        vm.scene.traverse(function (object) {
          if (object.isMesh) {
            meshes.push(object);
          } else if (object.isLine && dontRemove.indexOf(object.name) == -1) {
            lines.push(object);
          }
        });
        for (let i = 0; i < meshes.length; i++) {
          const mesh = meshes[i];
          mesh.material.dispose();
          mesh.geometry.dispose();
          vm.scene.remove(mesh);
        }
        for (let i = 0; i < lines.length; i++) {
          const line = lines[i];
          line.material.dispose();
          line.geometry.dispose();
          vm.scene.remove(line);
        }

        // add new meshes and re-render
        vm.addMeshes();
        vm.requestRenderIfNotRequested();
      },
      200,
      { leading: false }
    ),

    requestRenderIfNotRequested() {
      let vm = this;
      if (!vm.renderRequested) {
        vm.renderRequested = true;
        requestAnimationFrame(vm.render);
      }
    },

    resizeRendererToDisplaySize() {
      let vm = this;
      const pixelRatio = window.devicePixelRatio;
      const width = (vm.canvas.clientWidth * pixelRatio) | 0;
      const height = (vm.canvas.clientHeight * pixelRatio) | 0;
      const needResize =
        vm.canvas.width !== width || vm.canvas.height !== height;
      if (needResize) vm.renderer.setSize(width, height, false);
      return needResize;
    },

    render() {
      let vm = this;
      vm.renderRequested = false;
      if (vm.resizeRendererToDisplaySize()) {
        vm.camera.aspect = vm.canvas.clientWidth / vm.canvas.clientHeight;
        vm.camera.updateProjectionMatrix();
      }
      vm.controls.update();
      vm.renderer.render(vm.scene, vm.camera);
    },
  },
};
</script>

