  /** 
   * 100 view height -
   * 3 x 64px areas -
   * 1 x 48px area -
   * 24px margins and padding
   */
  width: 100%;
  height: calc(100vh - 3 * (64px) - 48px - 24px);
}

#c {
  width: 100%;
  height: 100%;
}

#interp-top-controls-container,
#interp-bottom-controls-container {
  height: 64px;
}

.cylinder-icon {
  fill: var(--v-primary-base);
}

.slider-border {
  border: 1px solid grey;
  border-radius: 4px;
}

.depth-slider-label {
  height: 1.5rem;
  text-align: center;
}

.depth-slider >>> .v-slider {
  /** 
   * 100 view height -
   * 3 x 64px areas -
   * 1 x 24px text -
   * 3 x 20px buttons -
   * 62px margins and padding
   */
  height: calc(100vh - 3 * (64px) - 24px - 3 * (20px) - 62px);
}

.depth-slider >>> .v-slider__thumb::before {
  left: -9px;
}

.depth-slider >>> .v-slider__thumb {
  width: 18px;
  left: -9px;
}
</style>
