<template>
  <v-app>
    <v-navigation-drawer
      fixed
      temporary
      disable-resize-watcher
      v-model="sideNavActive"
      :width="325"
    >
      <v-card outlined style="border: none">
        <v-card-title>Map Instructions (left)</v-card-title>
        <v-card-text>
          <span>
            1. <strong>Click and drag</strong> on the plot to zoom in to the
            dragged-over region.
          </span>
          <br /><br />
          <span>
            2. <strong>Right-click</strong> on the plot to undo the most recent
            zoom.
          </span>
          <br /><br />
          <span>
            3. <strong>Hover</strong> on a dot in the plot to see information
            about the cylinder.
          </span>
          <br /><br />
          <span>
            4. Click the <strong>Reset</strong> button to return the plot to
            it's original axis limits.
          </span>
          <br /><br />
          <span>
            <strong>Note:</strong> To get maps to display correctly, the x and y
            axes are each set to the larger extent of the two, which is the
            difference between the min and max values on each axis. However, the
            extent in SI units of length, calculated using the Haversine
            formula, may be different for each axis due to longitude spacing
            changing as a function of latitude.
          </span>
        </v-card-text>
      </v-card>

      <v-card outlined style="border: none">
        <v-card-title>3D Instructions (right)</v-card-title>
        <v-card-text>
          <span>1. Left-click and drag to rotate.</span>
          <br /><br />
          <span>2. Right-click and drag to pan.</span>
          <br /><br />
          <span>3. Middle-click and drag or scroll to zoom.</span>
          <br /><br />
        </v-card-text>
      </v-card>
    </v-navigation-drawer>

    <v-app-bar app>
      <v-row>
        <v-col cols="6">
          <v-row>
            <!-- INFO BUTTON -->
            <v-col cols="1" align-self="center" style="text-align: center">
              <v-icon @click="sideNavActive = !sideNavActive">
                mdi-information
              </v-icon>
            </v-col>

            <!-- CRUISE SELECT -->
            <v-col cols="11">
              <v-select
                outlined
                dense
                hide-details
                label="Cruise File"
                v-model="cruise.file"
                :menu-props="{ bottom: true, offsetY: true }"
                :items="cruise.files"
                @input="loadCruiseFile"
              ></v-select>
            </v-col>
          </v-row>
        </v-col>

        <v-col cols="6">
          <v-row>
            <!-- MODE SELECT -->
            <v-col cols="3" class="pr-1">
              <v-select
                outlined
                dense
                hide-details
                label="Mode"
                v-model="interp.mode"
                :menu-props="{ bottom: true, offsetY: true }"
                :items="interp.modes"
              ></v-select>
            </v-col>

            <!-- MODE: File -->
            <v-col cols="9" class="pl-1" v-if="interp.mode == 'File'">
              <!-- FILE SELECT -->
              <v-select
                outlined
                dense
                hide-details
                label="File"
                v-model="interp.file"
                :menu-props="{ bottom: true, offsetY: true }"
                :items="interp.files[cruise.file]"
                @input="loadInterpFile"
              ></v-select>
            </v-col>

            <!-- MODE: Manual -->
            <v-col
              cols="9"
              class="pl-1"
              v-if="interp.mode == 'Manual' && cylinders.selected.length"
            >
              <v-row>
                <!-- ATTRIBUTE SELECT -->
                <v-col cols="4" class="pr-1">
                  <v-select
                    outlined
                    dense
                    hide-details
                    multiple
                    label="Variables"
                    v-model="interp.variables.selected"
                    :menu-props="{
                      bottom: true,
                      offsetY: true,
                    }"
                    :items="interp.variables.all[cruise.file]"
                  >
                    <!-- SELECT ALL OPTION -->
                    <template v-slot:prepend-item>
                      <v-list-item ripple @click="toggleVariableMultiSelectAll">
                        <v-list-item-action>
                          <v-icon>
                            {{ variableMultiSelectAllIcon }}
                          </v-icon>
                        </v-list-item-action>
                        <v-list-item-content>
                          <v-list-item-title> Select All </v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                      <v-divider class="mt-2"></v-divider>
                    </template>

                    <!-- CUSTOM SELECTED TEXT -->
                    <template v-slot:selection="{ item, index }">
                      <span
                        v-if="index === 0"
                        :style="{
                          width:
                            interp.variables.selected.length > 1
                              ? '75%'
                              : '100%',
                          overflow: 'hidden',
                          'text-overflow': 'ellipsis',
                        }"
                      >
                        {{ item }}
                      </span>
                      <span
                        v-if="index === 1"
                        class="grey--text text-caption"
                        style="width: 25%; text-align: right"
                      >
                        (+{{ interp.variables.selected.length - 1 }})
                      </span>
                    </template>
                  </v-select>
                </v-col>

                <!-- INTERP METHOD MENU -->
                <v-col cols="4" class="px-1">
                  <v-menu
                    bottom
                    :offset-y="true"
                    :close-on-content-click="false"
                  >
                    <!-- MENU PREVIEW -->
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        outlined
                        dense
                        hide-details
                        readonly
                        label="Interpolation Method"
                        v-model="interpMethodMenuText"
                        v-on="on"
                        v-bind="attrs"
                      >
                        <template v-slot:append>
                          <v-icon v-on="on">mdi-menu-down</v-icon>
                        </template>
                      </v-text-field>
                    </template>

                    <!-- MENU CONTENTS -->
                    <v-card>
                      <v-container>
                        <v-row>
                          <v-col cols="6">
                            <v-select
                              outlined
                              dense
                              hide-details
                              label="Method"
                              v-model="interp.method"
                              :menu-props="{ bottom: true, offsetY: true }"
                              :items="interp.methods"
                            ></v-select>
                          </v-col>
                          <v-col cols="6">
                            <v-select
                              outlined
                              dense
                              hide-details
                              label="Resolution"
                              v-model="interp.resolution"
                              :menu-props="{ bottom: true, offsetY: true }"
                              :items="[7, 14, 21]"
                            >
                              <template v-slot:selection="{ item }">
                                <span>{{ item }}cm</span>
                              </template>
                              <template v-slot:item="{ item, on, attrs }">
                                <v-list-item v-on="on" v-bind="attrs">
                                  <v-list-item-content>
                                    <v-list-item-title>
                                      {{ item }}cm
                                    </v-list-item-title>
                                  </v-list-item-content>
                                </v-list-item>
                              </template>
                            </v-select>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card>
                  </v-menu>
                </v-col>

                <!-- INTERPOLATE BUTTON -->
                <v-col cols="4" class="pl-1">
                  <v-btn
                    block
                    small
                    style="height: 100%"
                    :disabled="
                      !(
                        interp.method &&
                        interp.resolution &&
                        interp.variables.selected.length
                      )
                    "
                    @click="getInterp"
                  >
                    Interpolate
                  </v-btn>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-app-bar>

    <v-main class="grey lighten-3">
      <v-container fluid id="view-area" style="max-height: calc(100vh - 64px)">
        <v-row>
          <v-col cols="6">
            <!-- MAP VIEW -->
            <MapView
              v-if="map.view.active"
              :cruise-file="cruise.file"
              :cruise-data="cruise.data"
              :interp-mode="interp.mode"
              :new-selected-cylinders="cylinders.selected"
              @updateSite="updateSite"
              @updateSelectedCylinders="updateSelectedCylinders"
            />
          </v-col>

          <v-divider vertical role="presentation"></v-divider>

          <v-col cols="6">
            <!-- FILE MODE -->
            <template v-if="interp.mode == 'File'">
              <InterpView
                v-if="interp.view.active"
                :interp-data="interp.data"
                :resolution="interp.resolution"
                :variables="interp.variables.all[cruise.file]"
              />
              <v-container v-else fluid class="fill-height">
                <v-row align="center" justify="center">
                  <span v-if="interp.view.loading">Loading...</span>
                  <span v-if="interp.view.failed">
                    Interpolation file failed to load. Please check the console
                    and the interpolation file and try again later.
                  </span>
                  <span v-if="!interp.view.loading && !interp.view.failed">
                    Choose a file to get started!
                  </span>
                </v-row>
              </v-container>
            </template>

            <!-- MANUAL MODE -->
            <template v-if="interp.mode == 'Manual'">
              <InterpView
                v-if="interp.view.active"
                :interp-data="interp.data"
                :resolution="interp.resolution"
                :variables="interp.variables.selected"
              />
              <template v-else>
                <CylinderView
                  v-if="
                    !interp.view.loading &&
                    !interp.view.failed &&
                    cylinders.selected.length
                  "
                  :cruise-data="cruise.data"
                  :new-selected-cylinders="cylinders.selected"
                  @updateSelectedCylinders="updateSelectedCylinders"
                />
                <v-container v-else fluid class="fill-height">
                  <v-row align="center" justify="center">
                    <span v-if="interp.view.loading">Loading...</span>
                    <span v-if="interp.view.failed">
                      Request for interpolation from backend failed. Please
                      check the console and server logs for possible errors and
                      try again.
                    </span>
                    <span v-if="!interp.view.loading && !interp.view.failed">
                      Pick cylinders on the left to get started!
                    </span>
                  </v-row>
                </v-container>
              </template>
            </template>
          </v-col>
        </v-row>
      </v-container>
    </v-main>

    <v-bottom-navigation
      fixed
      height="128px"
      scroll-threshold="10000"
      :input-value="bottomNavActive"
    >
      <v-card outlined id="bottom-nav-toggle-card" class="rounded-t-lg">
        <v-btn
          id="bottom-nav-toggle-btn"
          class="rounded-t-lg"
          :ripple="false"
          @click="bottomNavActive = !bottomNavActive"
        >
          <v-icon v-if="bottomNavActive">mdi-menu-down</v-icon>
          <v-icon v-if="!bottomNavActive">mdi-menu-up</v-icon>
        </v-btn>
      </v-card>

      <v-card id="bottom-nav-content-card" elevation="5">
        <v-card-actions>
          <v-btn>
            <span>Recents</span>
            <v-icon>mdi-history</v-icon>
          </v-btn>
          <v-btn>
            <span>Nearby</span>
            <v-icon>mdi-map-marker</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-bottom-navigation>
  </v-app>
</template>

<script>
import axios from "axios";
import * as csv from "jquery-csv";
import MapView from "./components/MapView";
import InterpView from "./components/InterpView";
import CylinderView from "./components/CylinderView";

export default {
  name: "App",

  components: {
    MapView,
    InterpView,
    CylinderView,
  },

  data: () => ({
    publicPath: process.env.BASE_URL,

    // cruise (root) parameters
    cruise: {
      file: "Pescadero_final.csv",
      files: ["Pescadero_final.csv", "SantaMonica_GeoChem_repeat.csv"],
      data: [],
      dataString: "",
    },

    // map view parameters
    map: {
      view: {
        active: false,
        loading: false,
      },
      site: "All",
    },

    // cylinder view parameters
    cylinders: {
      selected: [],
    },

    // interpolation view parameters
    interp: {
      view: {
        active: false,
        loading: false,
        failed: false,
      },
      variables: {
        selected: [],
        all: {
          "Pescadero_final.csv": [
            "Sulfide",
            "Ferrozine",
            "d13C_DIC",
            "DIC",
            "pH",
            "temp_10cmbsf",
            "temp_15cmbsf",
            "temp_30cmbsf",
            "Fluoride",
            "Acetate",
            "Formate",
            "Chloride",
            "Bromide",
            "Nitrate",
            "Sulfate",
            "Thiosulfate",
            "Phosphate",
            "Lithium",
            "Sodium",
            "Ammonium",
            "Potassium",
            "Magnesium",
            "Calcium",
            "Archaea;__Euryarchaeota;__Methanomicrobia;__ANME_1;__f;__g",
            "Archaea;__Euryarchaeota;__Methanomicrobia;__Methanosarcinales;__ANME_2c;__g",
            "Bacteria;__Proteobacteria;__Deltaproteobacteria;__Desulfobacterales;__Desulfobacteraceae;__SEEP_SRB1",
            "Bacteria;__Proteobacteria;__Deltaproteobacteria;__Desulfobacterales;__Desulfobulbaceae;__SEEP_SRB2",
            "Bacteria;__Proteobacteria;__Deltaproteobacteria;__Desulfobacterales;__Desulfobulbaceae;__SEEP_SRB4",
          ],
          "SantaMonica_GeoChem_repeat.csv": [
            "Ph",
            "Sodium",
            "Ammonium",
            "Potassium",
            "Magnesium",
            "Calcium",
            "Fluoride",
            "Lactate_Area",
            "Acetate",
            "Chloride",
            "Bromide",
            "Nitrate",
            "Carbonate",
            "Sulfate",
            "Thiosulfate",
            "Fumarate_Area",
          ],
        },
      },
      mode: "Manual",
      modes: ["Manual", "File"],
      file: null,
      files: {
        "Pescadero_final.csv": [
          "Pescadero_3d_natural_5cm.csv",
          "Pescadero_3d_natural_10cm.csv",
          "Pescadero_3d_natural_15cm.csv",
          "Pescadero_3d_linear_5cm.csv",
          "Pescadero_3d_linear_10cm.csv",
          "Pescadero_3d_linear_15cm.csv",
        ],
        "SantaMonica_GeoChem_repeat.csv": [],
      },
      method: null,
      methods: ["Natural", "Linear"],
      resolution: null,
      data: [],
    },

    // flags
    sideNavActive: false,
    bottomNavActive: false,
  }),

  computed: {
    interpMethodMenuText: function () {
      return this.interp.method && this.interp.resolution
        ? `${this.interp.method} → ${this.interp.resolution}cm`
        : "";
    },

    variableMultiSelectAllIcon() {
      return this.interp.variables.selected.length ==
        this.interp.variables.all[this.cruise.file].length
        ? "mdi-close-box"
        : this.interp.variables.selected.length > 0
        ? "mdi-minus-box"
        : "mdi-checkbox-blank-outline";
    },
  },

  mounted: function () {
    let vm = this;
    vm.loadCruiseFile();
  },

  watch: {
    "cruise.file": function () {
      let vm = this;
      vm.cylinders.selected = []; // remove selected cylinders
      vm.interp.view.active = false; // hide the interpolation view
      vm.interp.view.loading = false; // waiting for user to interpolate
      vm.interp.view.failed = false; // hide failure message
      vm.resetInterpParams(); // reset the inputs to the interpolation
    },
    "interp.mode": function () {
      let vm = this;
      vm.cylinders.selected = []; // remove selected cylinders
      vm.interp.view.active = false; // hide the interpolation view
      vm.interp.view.loading = false; // waiting for user to interpolate
      vm.interp.view.failed = false; // hide failure message
      vm.resetInterpParams(); // reset the inputs to the interpolation
    },
  },

  methods: {
    async loadFile(url) {
      const res = await fetch(url);
      return res.text();
    },

    loadCruiseFile() {
      let vm = this;
      vm.loadFile(`${vm.publicPath}assets/data/${vm.cruise.file}`).then(
        (text) => {
          // store text to send to backend for processing
          vm.cruise.dataString = text;
          // parse csv data, where each row becomes an object
          csv.toObjects(text, {}, (err, data) => {
            if (err) {
              console.log(err);
            } else {
              vm.cruise.data = data; // this should propogate to MapView.vue
              vm.map.view.active = true; // let the map view load
            }
          });
        }
      );
    },

    loadInterpFile() {
      let vm = this;
      vm.cylinders.selected = []; // clear any current cylinders selected
      vm.interp.view.active = false; // hide the component
      vm.interp.view.loading = true; // show loading icon
      vm.interp.view.failed = false; // hide failure message
      vm.loadFile(`${vm.publicPath}assets/data/${vm.interp.file}`)
        .then((text) => {
          // parse csv data, where each row becomes an object
          csv.toObjects(text, {}, (err, data) => {
            if (err) {
              console.log(err);
            } else {
              vm.cylinders.selected = [
                ...new Set(data.map((item) => item.Cylinder)),
              ]; // get cylinders in the data set
              vm.interp.resolution = parseInt(
                vm.interp.file.split(".")[0].split("_")[3].replace("cm", "")
              ); // get resolution from file name
              vm.interp.data = data; // this should propogate to InterpView.vue
              vm.interp.view.active = true; // show the component
              vm.interp.view.loading = false; // hide loading icon
            }
          });
        })
        .catch((err) => {
          console.log(err);
          vm.interp.view.failed = true; // show failure message
          vm.interp.view.loading = false; // hide loading icon
        });
    },

    getConnected() {
      axios({
        method: "GET",
        url: "http://localhost:8081/",
      }).then(function (res) {
        console.log(res.data);
      });
    },

    getInterp() {
      let vm = this;
      vm.interp.view.active = false; // hide the component
      vm.interp.view.loading = true; // show loading icon
      vm.interp.view.failed = false; // hide failure message
      const params = {
        cruise_data_string: vm.cruise.dataString, // send raw file string to backend
        method: vm.interp.method, // 'Linear' or 'Natural'
        res: vm.interp.resolution, // in cm
        get_dist: false,
        site: "All",
        cylinders: vm.cylinders.selected,
        variables: vm.interp.variables.selected,
      };
      axios({
        method: "POST",
        url: "http://localhost:8081/getInterp",
        data: params,
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      })
        .then(function (res) {
          vm.interp.data = res.data; // this should propogate to InterpView.vue
          vm.interp.view.active = true; // show the component
          vm.interp.view.loading = false; // hide loading icon
        })
        .catch(function (err) {
          console.log(err);
          vm.interp.view.failed = true; // show failure message
          vm.interp.view.loading = false; // hide loading icon
        });
    },

    resetInterpParams() {
      this.interp.variables.selected = [];
      this.interp.file = null;
      this.interp.method = null;
      this.interp.resolution = null;
    },

    toggleVariableMultiSelectAll() {
      let vm = this;
      vm.$nextTick(() => {
        vm.interp.variables.selected =
          vm.interp.variables.selected.length ==
          vm.interp.variables.all[vm.cruise.file].length
            ? []
            : vm.interp.variables.all[vm.cruise.file].slice();
      });
    },

    updateSite(newSiteName) {
      this.map.site = newSiteName;
    },

    updateSelectedCylinders(newSelectedCylinders) {
      // if another component updates the cylinders, hide the interpolation view
      let vm = this;
      vm.cylinders.selected = newSelectedCylinders;
      vm.interp.view.active = false; // hide the interpolation view
      vm.interp.view.loading = false; // waiting for user to interpolate
      vm.interp.view.failed = false;
      vm.resetInterpParams(); // reset the inputs to the interpolation
    },
  },
};
</script>

<style>
html {
  overflow-y: hidden !important;
}

#bottom-nav-toggle-card {
  position: absolute;
  z-index: 10000;
  width: 50px;
  height: 28px;
  margin-top: -26px;
  border: 2px solid black;
  border-bottom: 2px solid white;
  border-top-left-radius: 75% !important;
  border-top-right-radius: 75% !important;
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

#bottom-nav-toggle-btn {
  min-width: 100%;
  padding: 0px;
  border-top-left-radius: 75% !important;
  border-top-right-radius: 75% !important;
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

#bottom-nav-toggle-btn::before {
  background-color: transparent !important;
}

#bottom-nav-content-card {
  width: 100%;
  border-top: 2px solid black;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>
